@charset "UTF-8";
@font-face {
  font-family: 'TT Lakes';
  font-weight: normal;
  font-style: normal;
  src: url(./fonts/TTLakes-Regular.ttf); }

@font-face {
  font-family: 'TT Lakes';
  font-weight: 500;
  font-style: normal;
  src: url(./fonts/TTLakes-Medium.ttf); }

@font-face {
  font-family: 'TT Lakes';
  font-weight: bold;
  font-style: normal;
  src: url(./fonts/TTLakes-Bold.ttf); }

@font-face {
  font-family: 'TT Lakes';
  font-weight: 300;
  font-style: normal;
  src: url(./fonts/TTLakes-Light.ttf); }

* {
  margin: 0;
  padding: 0; }

body {
  font-family: 'TT Lakes';
  font-weight: normal;
  background-color: #f8f8f8;
  max-width: 1920px;
  margin: 0 auto; }

.item--hidden {
  display: none; }

.title {
  font-size: 24px; }
  .title::before {
    content: '';
    border: 3px solid #41f6d7;
    background-color: #41f6d7;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    margin-right: 12px; }

.read-more {
  font-size: 16px;
  font-weight: 500;
  text-decoration: none;
  color: black; }
  .read-more::before {
    content: url(./static/expand.svg);
    display: inline-block;
    margin-right: 8px;
    vertical-align: middle;
    transition: 0.2s; }
  .read-more--clicked::before {
    transform: rotate(180deg);
    transform-origin: 50% 43%; }

#overlay {
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.96);
  position: fixed;
  z-index: 2; }

.overlay--hidden {
  display: none; }

.divider {
  height: 32px;
  border: 1px solid #eaeaea;
  background-color: #eaeaea;
  border-radius: 1px; }
  .divider--tablet-only {
    display: none; }
  .divider-not-desctop {
    margin-left: auto;
    margin-right: 4px; }

.logo {
  width: 100px;
  height: 53px; }
  .logo_image {
    width: 100%; }

.button {
  display: block;
  width: 40px;
  height: 40px;
  padding: 0;
  border: 0;
  border-radius: 50%;
  cursor: pointer;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }
  .button:hover {
    transition: 0.2s;
    opacity: 0.8; }
  .button_image {
    width: 100%; }
  .button--closer {
    background: #ff3e79 url(./static/closer.svg) no-repeat center center; }
  .button--searching {
    background: #ff3e79 url(./static/searching.svg) no-repeat center center; }
  .button--call {
    background: #ff3e79 url(./static/call.svg) no-repeat center center; }
  .button--chat {
    background: #ff3e79 url(./static/chat.svg) no-repeat center center; }
  .button--profile {
    background: #ff3e79 url(./static/profile.svg) no-repeat center center; }
  .button--menu {
    background-color: #ff3e79 no-repeat center center; }
  .button_menu {
    margin: 0 16px 0 16px; }

.buttons-action {
  display: flex; }

.buttons-action_button {
  margin-left: 16px; }

.swiper-pagination {
  position: static;
  margin-top: 16px; }

.swiper-pagination-bullet {
  width: 12px;
  height: 12px;
  background-color: #ccc9c9; }

.swiper-pagination-bullet-active {
  background-color: #b5b6bc; }

.modal {
  min-width: 320px;
  max-width: 425px;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  right: -110%;
  box-shadow: 16px 0px 52px rgba(14, 24, 80, 0.4);
  background-color: #ffffff;
  transition: ease-out 0.4s;
  overflow: auto;
  z-index: 4;
  box-sizing: border-box; }
  .modal--open {
    right: 0; }
  .modal_button {
    margin: 24px 0 40px 16px; }
  .modal_title {
    margin-bottom: 24px; }
  .modal_input {
    width: 84%;
    margin: 0 16px 16px 16px;
    height: 48px;
    border: 1px solid #eaeaea;
    border-radius: 8px;
    padding-left: 16px; }
  .modal_input--message {
    height: 118px; }
  .modal_input--message::-webkit-input-placeholder {
    line-height: 50px; }
  .modal_input::-webkit-input-placeholder {
    font-family: 'TT Lakes';
    font-size: 16px; }
  .modal_approvement {
    margin: 0 16px 0 16px;
    font-size: 14px;
    color: #7e7e82; }
  .modal_approvement-link {
    text-decoration: none;
    color: #ff3e79; }
  .modal_button-submit {
    font-family: 'TT Lakes';
    font-weight: 500;
    width: 136px;
    height: 32px;
    font-size: 12px;
    border-radius: 16px;
    margin: 23px 16px 75px auto;
    background-color: #ff3e79;
    color: white;
    text-transform: uppercase; }

.repair-item_button {
  position: relative;
  background-color: inherit;
  border: 2px solid #ff3e79;
  border-radius: 50%;
  width: 40px;
  height: 40px; }

.repair-item_button::before {
  content: url(./static/dropdown.svg);
  line-height: 42px;
  margin-left: 3px; }

.h3_wrapper {
  height: 80px;
  background-image: linear-gradient(to right, #f8f8f8, white);
  display: flex;
  align-items: center;
  margin-bottom: 16px; }

.h3_title {
  font-weight: 300;
  text-transform: uppercase;
  font-size: 16px;
  line-height: 24px;
  margin-left: 32px;
  color: #7e7e82;
  vertical-align: middle; }

.prices_swiper::before {
  content: '';
  display: block;
  position: absolute;
  background-color: #f8f8f8;
  box-shadow: 0 0px 10px 9px #f8f8f8;
  right: 0;
  width: 5px;
  height: 200px;
  z-index: 2; }

.prices_slide {
  width: 260px;
  height: 200px;
  box-sizing: border-box;
  padding: 16px;
  margin-left: 16px;
  border: 1px solid #eaeaea;
  border-radius: 6px;
  position: relative; }

.prices_service {
  margin-bottom: 16px; }

.prices_price {
  margin-bottom: 16px; }

.prices_title {
  font-size: 14px;
  font-weight: normal;
  color: #7e7e82; }

.prices_text {
  font-size: 14px;
  color: black; }

.prices_button {
  width: 119px;
  height: 32px;
  background-color: #ff3e79;
  color: white;
  font-size: 12px;
  font-weight: 500;
  text-transform: uppercase;
  font-family: 'TT Lakes';
  border-radius: 16px;
  position: absolute;
  right: 16px;
  bottom: 16px; }

.prices_alert {
  margin: 16px 50px 20px 16px;
  line-height: 20px; }

.prices_commercial {
  display: block;
  margin-left: 16px;
  margin-bottom: 50px;
  font-size: 17px;
  line-height: 24px;
  font-weight: 500;
  text-decoration: none;
  color: black; }

.prices_commercial::after {
  content: url(./static/dropdown.svg);
  margin-left: 10px;
  vertical-align: middle; }

.tech-repair {
  margin-bottom: 32px; }
  .tech-repair_swiper::before {
    content: '';
    display: block;
    position: absolute;
    background-color: #f8f8f8;
    box-shadow: 0 0px 10px 9px #f8f8f8;
    right: 0;
    width: 5px;
    height: 160px;
    z-index: 2; }
  .tech-repair_slide {
    position: relative;
    box-sizing: border-box;
    padding: 16px;
    margin-left: 16px;
    width: 240px;
    height: 160px;
    background-color: #ffffff;
    border: 1px solid #eaeaea;
    border-radius: 6px; }
  .tech-repair_item {
    font-weight: 500; }
  .tech-repair_button {
    position: absolute;
    right: 16px;
    bottom: 16px; }

.brends-repair {
  margin-bottom: 50px; }
  .brends-repair_swiper::before {
    content: '';
    display: block;
    position: absolute;
    background-color: #f8f8f8;
    box-shadow: 0 0px 10px 9px #f8f8f8;
    right: 0;
    width: 5px;
    height: 70px;
    z-index: 2; }
  .brends-repair_slide {
    box-sizing: border-box;
    width: 240px;
    height: 72px;
    padding: 16px;
    margin-left: 16px;
    background-color: white;
    border: 1px solid #eaeaea;
    border-radius: 6px;
    justify-content: space-between;
    display: flex; }
  .brends-repair_img {
    max-width: 128px;
    transform: scale(1.1); }

.main-page_title {
  font-size: 28px;
  margin-bottom: 24px; }

.main-page_nav {
  display: flex;
  flex-wrap: nowrap;
  overflow-x: auto;
  margin: 0 16px 24px 16px;
  padding: 4px; }

.main-page_nav::-webkit-scrollbar {
  display: none; }

.main-page_nav::after {
  content: '';
  display: block;
  position: absolute;
  background-color: #f8f8f8;
  box-shadow: 0 0px 10px 9px #f8f8f8;
  right: 15px;
  width: 2px;
  height: 40px; }

.main-page_nav::before {
  content: '';
  display: block;
  position: absolute;
  background-color: #f8f8f8;
  box-shadow: 0 0px 10px 9px #f8f8f8;
  left: 15px;
  width: 2px;
  height: 40px; }

.main-page_nav-button {
  width: auto;
  height: auto;
  flex: 0 0 auto;
  padding: 8px 22px;
  margin: 2px;
  font-family: 'TT Lakes';
  font-size: 16px;
  background-color: #f8f8f8;
  color: #7e7e82;
  border: 2px solid #f8f8f8; }

.main-page_nav-button:focus {
  background-color: #ffffff;
  border: 2px solid #41f6d7;
  border-radius: 6px; }

.main-page_content {
  margin: 0 0 24px 0; }

.main-page_content-article {
  font-size: 15px;
  height: 100px;
  overflow: hidden;
  margin: 0 16px 10px 16px; }

.main-page_content-article--open {
  height: 100%; }

.main-page_read-more {
  display: block;
  margin-left: 16px;
  margin-bottom: 16px; }

.main-page_content-img {
  width: 100%; }

.header {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  max-width: 1366px;
  height: 88px;
  margin-bottom: 24px;
  background-color: white; }
  .header_buttons-action {
    position: absolute;
    z-index: -1; }
  .header_repair {
    margin: 0 16px 0 16px; }
  .header_check-status {
    margin-right: 16px; }
  .header_logo {
    margin-right: 16px; }

.side-menu {
  min-width: 320px;
  max-width: 425px;
  width: 100%;
  min-height: 100vh;
  position: fixed;
  overflow: auto;
  left: -110%;
  z-index: 4;
  transition: ease-out 0.4s;
  box-shadow: 16px 0px 52px rgba(14, 24, 80, 0.2);
  background-color: #ffffff;
  font-weight: 500;
  /* phone & mail */
  /* lang-switch */ }
  .side-menu::-webkit-scrollbar {
    display: none; }
  .side-menu_wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    max-height: 1024px;
    position: fixed;
    width: 100%;
    overflow: auto; }
  .side-menu_side-header {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-top: 18px; }
  .side-menu_button {
    margin-left: 16px; }
  .side-menu_logo {
    margin-left: 16px; }
  .side-menu_button:last-child {
    margin-left: auto;
    margin-right: 16px; }
  .side-menu_navigation-list {
    margin-bottom: auto;
    margin-top: 20px; }
  .side-menu_nav-item {
    list-style: none;
    margin-bottom: 24px;
    margin-left: 24px;
    left: 20px; }
    .side-menu_nav-item .nav-item_link {
      text-decoration: none;
      color: black;
      -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }
    .side-menu_nav-item .nav-item_link::before {
      content: '';
      position: absolute;
      left: -5px;
      height: 1.25em;
      border: 3px solid #41f6d7;
      border-top-right-radius: 5px;
      border-bottom-right-radius: 5px; }
    .side-menu_nav-item .nav-item_link:hover::before,
    .side-menu_nav-item .nav-item_link:focus::before {
      left: 0;
      transition: ease-in-out 0.3s; }
    .side-menu_nav-item .nav-item_link:hover,
    .side-menu_nav-item .nav-item_link:focus {
      color: #7e7e82;
      transition: 0.2s; }
  .side-menu_buttons-action {
    margin-bottom: 16px; }
  .side-menu_phone-and-mail {
    font-size: 24px;
    font-weight: bold;
    line-height: 32px;
    letter-spacing: -0.6px;
    color: #1b1c21;
    margin-left: 16px;
    margin-bottom: 8px;
    text-decoration: none; }
  .side-menu_phone-and-mail::before {
    content: attr(data-mail);
    display: block;
    font-size: 16px;
    margin-bottom: 12px;
    font-weight: 500; }
  .side-menu_lang-switch {
    margin-bottom: 32px;
    margin-left: 15px; }
    .side-menu_lang-switch .lang-btn {
      border: none;
      cursor: pointer;
      background-color: inherit;
      text-transform: uppercase;
      font-size: 16px;
      -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }
    .side-menu_lang-switch .lang-btn:hover {
      color: #7e7e82;
      transition: 0.2s; }
    .side-menu_lang-switch input {
      position: absolute;
      opacity: 0;
      z-index: -1; }

.side-menu--active {
  left: 0; }

.footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: 130px;
  margin-bottom: 19px; }
  .footer_confidence, .footer_copyright, .footer_rights {
    color: #7e7e82;
    font-size: 14px;
    text-decoration: none;
    text-align: center; }

@media (min-width: 426px) {
  .side-menu {
    width: 320px; } }

@media (min-width: 768px) {
