    display: block; }
  .header_logo {
    margin-left: 16px; }
  .header_buttons-action {
    z-index: 1;
    right: 150px; }
  .main-page_content {
    display: flex;
    align-items: flex-start;
    max-width: 1200px; }
  .main-page_content-wrapper {
    flex-basis: 50%;
    flex-grow: 1; }
  .main-page_content-img {
    margin-right: 24px;
    flex-basis: 50%;
    flex-grow: 1; }
  .brends-repair_list,
  .tech-repair_list {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    margin: 0 auto;
    margin-top: 16px;
    width: 95%;
    height: 160px;
    overflow: hidden; }
  .brends-repair_slide,
  .tech-repair_slide {
    margin: 0 0.5em 0.5em 0.5em;
    width: 30%; }
  .brends-repair_read-more,
  .tech-repair_read-more {
