@media (min-width: 1366px) {
  body {
    display: flex;
    position: relative; }
  .header {
    position: absolute;
    top: -100px; }
    .header_repair {
      position: absolute;
      top: 140px;
      right: 250px; }
    .header_repair::before {
      content: 'Оставить заявку';
      position: absolute;
      left: -140px;
      top: 8px;
      font-family: 'TT Lakes';
      font-size: 16px;
      font-weight: 500; }
    .header_check-status {
      position: absolute;
      top: 140px;
      right: 30px; }
    .header_check-status::before {
      content: 'Статус ремонта';
      position: absolute;
      left: -133px;
      top: 8px;
      font-family: 'TT Lakes';
      font-size: 16px;
      font-weight: 500; }
  .main-page_title {
    margin-top: 42px; }
  .main-page_nav {
    flex-wrap: wrap; }
  .side-menu {
    position: static;
    box-shadow: none; }
    .side-menu .button--closer {
      display: none; }
  .brends-repair_list,
  .tech-repair_list {
